

























































































import {
	mapState
} from 'vuex'
var _self;
var graceChecker = require("../../../graceUI/graceChecker.js");
export default {
	data() {
		return {
			idCard1: 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/idcard1.png',
			idCard2: 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/idcard2.png',
			idCard3: 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/schoolcard.png',
			name: '',
			idNum: ''
		};
	},
	computed: mapState(['user']),
	onLoad: function() {
		_self = this;
	},
	methods: {
		selectImg1: function() {
			uni.chooseImage({
				count: 1,
				success: function(res) {
					uni.showLoading({
						title: "上传中"
					});
					uni.uploadFile({
						url: _self.GLOBAL.serverSrc + 'market/index/upload',
						filePath: res.tempFilePaths[0],
						name: 'card_front',
						success: function(uploadFileRes) {
							var resObj = JSON.parse(uploadFileRes.data);
							if (resObj.status === 200) {
								_self.idCard1 = resObj.imagePath;
							} else {
								uni.showToast({
									title: resObj.msg,
									icon: "none"
								});
							}
						},
						fail: function() {
							uni.showToast({
								title: '上传失败,请检查网络稍后重试！',
								icon: "none"
							});
						},
						complete: function() {
							uni.hideLoading();
						}
					});
				}
			});
		},
		selectImg2: function() {
			uni.chooseImage({
				count: 1,
				success: function(res) {
					uni.showLoading({
						title: "上传中"
					});
					uni.uploadFile({
						url: _self.GLOBAL.serverSrc + 'market/index/upload',
						filePath: res.tempFilePaths[0],
						name: 'card_back',
						success: function(uploadFileRes) {
							var resObj = JSON.parse(uploadFileRes.data);
							if (resObj.status === 200) {
								_self.idCard2 = resObj.imagePath;
							} else {
								uni.showToast({
									title: resObj.msg,
									icon: "none"
								});
							}
						},
						fail: function() {
							uni.showToast({
								title: '上传失败,请检查网络稍后重试！',
								icon: "none"
							});
						},
						complete: () => {
							uni.hideLoading();
						}
					});
				}
			});
		},
		selectImg3: function() {
			uni.chooseImage({
				count: 1,
				success: function(res) {
					uni.uploadFile({
						url: _self.GLOBAL.serverSrc + 'market/index/upload',
						filePath: res.tempFilePaths[0],
						name: 'student_card',
						success: function(uploadFileRes) {
							var resObj = JSON.parse(uploadFileRes.data);
							if (resObj.status === 200) {
								_self.idCard3 = resObj.imagePath;
							} else {
								uni.showToast({
									title: resObj.msg,
									icon: "none"
								});
							}
						},
						fail: function() {
							uni.showToast({
								title: '上传失败,请检查网络稍后重试！',
								icon: "none"
							});
						},
						complete: () => {
							uni.hideLoading();
						}
					});
				}
			});
		},
		previewImg1: function() {
			uni.previewImage({
				urls: [_self.idCard1]
			});
		},
		previewImg2: function() {
			uni.previewImage({
				urls: [_self.idCard2]
			});
		},
		previewImg3: function() {
			uni.previewImage({
				urls: [_self.idCard3]
			});
		},
		uploadCards: function() {
			if (this.idCard1 == 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/idcard1.png' || this.idCard2 ==
				'https://yuange666.oss-cn-beijing.aliyuncs.com/app/idcard2.png') {
				uni.showToast({
					title: "请上传身份证照片",
					icon: "none"
				});
				return;
			} else if (this.idCard3 == 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/schoolcard.png') {
				uni.showToast({
					title: "请上传学生证证照片",
					icon: "none"
				});
				return;
			} else {
				var rule = [{
						name: "name",
						checkType: "string",
						checkRule: "2,4",
						errorMsg: "请输入正确的姓名"
					},
					{
						name: "idNum",
						checkType: "notnull",
						checkRule: "18,18",
						errorMsg: "请输入正确的身份证号"
					}
				];
				//进行表单检查
				var formData = {
					name: this.name,
					idNum: this.idNum
				};
				var checkRes = graceChecker.check(formData, rule);
				if (checkRes) {
					uni.showLoading({
						title: "提交中..."
					});
					uni.request({
						url: this.GLOBAL.serverSrc + 'market/index/insertinfo',
						method: 'POST',
						data: {
							user_id:this.user.id,
							phone : this.user.phone,
							token:this.user.token,
							verify_name:this.name,
							id_number:this.idNum,
							card_front:this.idCard1,
							card_back:this.idCard2,
							student_card:this.idCard3
						},
						success: res => {
							if(res.data.status === 200){
								uni.redirectTo({
									url:'./index'
								});
							}else{
								uni.showToast({
									title: res.data.msg,
									icon: "none"
								});
							}
							console.log(JSON.stringify(res));
						},
						fail: (e) => {
							this.GLOBAL.requestFail(e);
						},
						complete: () => {
							uni.hideLoading();
						}
					});
				} else {
					uni.showToast({
						title: graceChecker.error,
						icon: "none"
					});
				}
			}
		}
	},
}
